.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;

  overflow: hidden;

  @media (max-width: $to-desktop-width) {
    position: static;

    overflow: visible;
  }
}

.scroll-lock {
  overflow: hidden;

  @media (max-width: $to-desktop-width) {
    overflow: visible;
  }
}
